body {
  font-family: 'Arial', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f0f0f0;
  margin: 0;
}

.game {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

.game-board {
  display: grid;
  grid-template-columns: repeat(15, 30px);
  grid-template-rows: repeat(15, 30px);
  gap: 1px;
}

.square {
  width: 30px;
  height: 30px;
  background-color: #fff;
  border: 1px solid #999;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5em;
  cursor: pointer;
}

.game-info {
  margin-top: 20px;
}

.status {
  margin-bottom: 10px;
}

.winning-square {
  background-color: #ff0;
}